import { getElement } from "../element/element";
import './order.css';

export function getOrder() {
    const orderBlock = getElement('form', 'order');
    const container = getElement('div', 'container', 'order__container');
    const orderTitle = getElement('h3', 'order__title');
    orderTitle.textContent = 'Заказать фотосессию';
    const orderText = getElement('p', 'order__descr'); 
    orderText.textContent = 'Укажите ваши данные';
    const inpName = getElement('input', 'order__name', 'order__input');
    inpName.required = 'required';
    inpName.type = 'text';
    inpName.placeholder = 'ФИО';
    const inpNumber = getElement('input', 'order__number', 'order__input');
    inpNumber.required = 'required';
    inpNumber.type = 'text';
    inpNumber.placeholder = 'Номер телефона';
    const inpText = getElement('textarea', 'order__text', 'order__input');
    inpText.placeholder = 'Сообщение :)';
    const submitBtn = getElement('button', 'order__submit');
    
    submitBtn.textContent = 'Отправить заявку';
    submitBtn.type = 'submit';

    container.append(orderTitle, orderText, inpName, inpNumber, inpText, submitBtn);
    orderBlock.append(container);
    return orderBlock;
}