import { getElement } from "../../components/element/element";
import { getOrder } from "../../components/order/order";
import './home.css';
import aboutSrc from "/src/assets/img/about.jpg";
import { router } from "../../main";

export function getHome() {
    const home = getElement('div', 'home');
    const container = getElement('div', 'container', 'home__container');
    const heroSection = getElement('section', 'hero', 'home__hero');
    const heroTitle = getElement('h1', 'hero__title');
    const heroText = getElement('p', 'hero__text');
    heroText.innerText = 'Capturing the unseen rhythms of the city.';
    const heroLink = getElement('a', 'hero__link');
    heroLink.addEventListener("click", function (event) {
        event.preventDefault()
        router.navigate('/portfolio');
    });
    heroLink.innerText = 'Портфолио';
    heroTitle.innerHTML = 'Какой-то текст <br> фотограф';
    const coverBox = getElement('div', 'hero__cover');
    const aboutSection = getElement('section', 'about', 'home__about');
    const aboutTitle = getElement('h3', 'about__title');
    aboutTitle.innerText = 'Обо мне';
    const aboutContent = getElement('div', 'about__content');
    const aboutLeft = getElement('div', 'about__left');
    const aboutRight = getElement('div', 'about__right');
    const aboutText = getElement('p', 'about__text');
    aboutText.textContent = `A connoisseur of cityscapes and a storyteller through the lens, based in the vibrant heart of London. 
    With over a decade of experience, I have been capturing the essence of urban life, one frame at a time.`;

    const aboutImage = getElement('img', 'about__img');
    aboutImage.src = aboutSrc;

    const order = getOrder();

    aboutRight.append(aboutImage);
    aboutLeft.append(aboutTitle,aboutText);
    aboutContent.append(aboutLeft, aboutRight);
    aboutSection.append(aboutContent);
    heroSection.append(heroTitle, heroText, heroLink, coverBox);
    container.append(heroSection, aboutSection);
    home.append(container, order);
    return home;
}